import React, {Component} from 'react';
import PropTypes from "prop-types";
import {connect} from "react-redux"
import classnames from "classnames";
import {createProject, getProject} from "../../actions/projectActions";

class UpdateProject extends Component {

    constructor() {
        super()

        this.state = {
            id: "",
            projectName: "",
            projectIdentifier: "",
            description: "",
            startDate: "",
            endDate: "",
            errors: {}
        }
        this.onChange = this.onChange.bind(this);
        this.onSubmit = this.onSubmit.bind(this);
    }

    componentWillReceiveProps(nextProps) {
        if (nextProps.errors) {
            this.setState({errors: nextProps.errors});
        }
        const {
            id,
            projectName,
            projectIdentifier,
            description,
            startDate,
            endDate
        } = nextProps.project;
        this.setState({
            id,
            projectName,
            projectIdentifier,
            description,
            startDate,
            endDate
        });
    }

    componentDidMount() {
        const {id} = this.props.match.params;
        this.props.getProject(id, this.props.history);
    }

    onChange(e) {
        this.setState({[e.target.name]: e.target.value});
    }

    onSubmit(e) {
        e.preventDefault();
        const updateProject = {
            id: this.state.id,
            projectName: this.state.projectName,
            projectIdentifier: this.state.projectIdentifier,
            description: this.state.description,
            startDate: this.state.startDate,
            endDate: this.state.endDate
        }
        this.props.createProject(updateProject, this.props.history);
    }

    render() {
        const {errors} = this.state;
        return (
            <div className="project">
                <div className="container">
                    <div className="row">
                        <div className="col-md-8 m-auto">
                            <h5 className="display-4 text-center">Update Project</h5>
                            <hr/>
                            <form onSubmit={this.onSubmit}>
                                <div className="form-group">
                                    <input type="text" className={classnames("form-control form-control-sm", {
                                        "is-invalid": errors.projectName
                                    })}
                                           name="projectName"
                                           value={this.state.projectName}
                                           onChange={this.onChange}
                                           placeholder="Project Name"/>
                                    {errors.projectName && (
                                        <div className="invalid-feedback">
                                            {errors.projectName}
                                        </div>
                                    )}
                                </div>
                                <div className="form-group">
                                    <input type="text"
                                           className={classnames("form-control form-control-sm", {
                                               "is-invalid": errors.projectIdentifier
                                           })}
                                           name="projectIdentifier"
                                           value={this.state.projectIdentifier}
                                           placeholder="Unique Project ID" disabled/>
                                </div>
                                <div className="form-group">
                                    <textarea
                                        className={classnames("form-control form-control-sm", {
                                            "is-invalid": errors.description
                                        })}
                                        name="description"
                                        value={this.state.description}
                                        onChange={this.onChange}
                                        placeholder="Project Description"></textarea>
                                    {errors.description && (
                                        <div className="invalid-feedback">
                                            {errors.description}
                                        </div>
                                    )}
                                </div>
                                <h6>Start Date</h6>
                                <div className="form-group">
                                    <input type="date"
                                           className="form-control form-control-sm" name="startDate"
                                           onChange={this.onChange}
                                           value={this.state.startDate}
                                    />
                                </div>
                                <h6>Estimated End Date</h6>
                                <div className="form-group">
                                    <input type="date" className="form-control form-control-sm" name="endDate"
                                           onChange={this.onChange}
                                           value={this.state.endDate}
                                    />
                                </div>
                                <input type="submit" className="btn btn-primary btn-block mt-sm-4"/>
                            </form>
                        </div>
                    </div>
                </div>
            </div>
        );
    }
}

UpdateProject.propTypes = {
    getProject: PropTypes.func.isRequired,
    createProject: PropTypes.func.isRequired,
    project: PropTypes.object.isRequired,
    errors: PropTypes.object.isRequired
}

const mapStateToProps = state => ({
    project: state.project.project,
    errors: state.errors
});

export default connect(mapStateToProps, {getProject, createProject})(UpdateProject);


